.home-page .text-zone {
  position: absolute;
  left: 23%;
  top: 50%;
  transform: translateY(-50%);
  width: 40%;
  max-height: 90%;
  min-width: 300px;
}
.home-page h1 {
  color: #fff;
  font-size: 53px;
  margin: 0;
  font-family: "Coolvetica";
  font-weight: 400;
}
.home-page h1::before {
  content: "<h1>";
  font-family: "La Belle Aurore";
  color: #ffd700;
  font-size: 18px;
  position: absolute;
  margin-top: -20px;
  left: 15px;
  opacity: 0.6;
}
.home-page h1::after {
  content: "</h1>";
  font-family: "La Belle Aurore";
  color: #ffd700;
  opacity: 0.6;
  font-size: 18px;
  position: relative;
  margin-top: 18px;
  margin-left: 20px;
}
.home-page h1 img {
  width: 32px;
  margin-left: 10px;
  right: 10px;
  opacity: 0;
  height: auto;
  animation: rotateIn 1s linear both;
  animation-delay: 1.4s;
}
.home-page h2 {
  color: #8d8d8d;
  margin-top: 20px;
  font-weight: 400;
  font-size: 11px;
  font-family: sans-serif;
  letter-spacing: 3px;
  animation: fadeIn 1s 1.8s backwards;
}
.home-page .flat-button {
  color: #ffd700;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 4px;
  font-family: sans-serif;
  text-decoration: none;
  padding: 10px 18px;
  border: 1px solid #ffd700;
  margin-top: 25px;
  float: left;
  animation: fadeIn 1s 1.8s backwards;
  white-space: nowrap;
}
.home-page .flat-button:hover {
  background: #ffd700;
  color: #333;
}